@import "bourbon/_bourbon.scss";

@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,400i,700,700i,800');
body { font-family: 'Open Sans', sans-serif; font-size: 16px;}
h1 { font-weight: 900; color:#444; font-size: 3em;
	b { font-weight: 900; background: #1e7147; background: #1ecc74; color: #fff; display: inline-block; padding: 6px 16px; }
}
h2 { font-weight: 900; color:#444; font-size: 2.5em; }
h3 { font-weight: 900; color:#444; font-size: 2em; }
h4 { font-weight: 900; color:#444; font-size: 1.5em; }
h5 { font-weight: 900; color:#444; font-size: 1em; }
h6 { font-weight: 900; color:#444; font-size: 0.9em; }

.checkbox input[type=checkbox], .checkbox-inline input[type=checkbox], .radio input[type=radio], .radio-inline input[type=radio] {
	outline: none;
}
.checkbox, .radio {margin-top: 0; }
